/*кнопки начать/пропустить в форме геолокации прелендинга создаем сначала кнопку пропустить, затем делаем extend всех ее свойств в кнопку начать.*/
.pr-header__geolocation-skip-button, .pr-header__geolocation-start-button { display: block; width: calc(100% - 10px); max-width: 100%; padding: 23px 0; border-radius: 4px; background: rgba(0, 0, 0, 0.5); border: 0; outline: 0; color: #fff !important; text-decoration: none !important; cursor: pointer; font-size: 18px; }

/*-----------------------------------------------------------*/
/*кнопки мобильных маркетов в прелендинге, создаем сначала кнопку appstore
и затем делаем extend всех ее свойств в кнопку google play*/
.pr-promo__appstore, .pr-promo__google-play { position: relative; display: inline-block; margin-bottom: 20px; }

.pr-promo__appstore:before, .pr-promo__google-play:before { content: ""; display: block; width: 100%; height: 100%; position: absolute; top: 0; left: 0; background: rgba(0, 0, 0, 0.1); border-radius: 10px; }

/*кнопки социальных сетей футера*/
.footer__social-network-button { position: relative; display: block; width: 46px; height: 46px; flex-shrink: 0; border-radius: 10px; margin-left: 15px; }

.footer__social-network-button:first-child { margin-left: 0; }

.footer__social-network-button:before { content: ""; display: block; width: 100%; height: 100%; position: absolute; top: 0; left: 0; background: rgba(0, 0, 0, 0.1); border-radius: 10px; }

.footer__social-network-button_vk { background: url(../img/vk_icon.png) no-repeat; background-size: cover; }

.footer__social-network-button_fb { background: url(../img/fb_icon.png) no-repeat; background-size: cover; }

.footer__social-network-button_inst { background: url(../img/inst_icon.png) no-repeat; background-size: cover; }

.footer__social-network-button_yout { background: url(../img/youtube.png) no-repeat; background-size: cover; }

/*-----------------------------------------------------------*/
/*основные кнопки которые задаются миксином и при наведении/нажатии становятся темнее*/
/*кнопки мобильных маркетов в футере, создаем сначала кнопку appstore,
задаем background-image спрайт из двух кнопок и
затем делаем extend всех ее свойств в кнопку google play,
за исключением того что двигаем cпрайт.*/
.footer__appstore, .footer__google-play { display: block; width: 128px; height: 40px; position: relative; border: 1px solid #fff; border-radius: 4px; background-image: url(../img/app_icons.png); margin-bottom: 10px; }

.footer__appstore:before, .footer__google-play:before { content: ""; display: block; width: 100%; height: 100%; position: absolute; top: 0; left: 0; background: rgba(0, 0, 0, 0.1); }

.footer__google-play { background-position: 128px 0; margin-left: 15px; }

/*-----------------------------------------------------------*/
.test-button { display: block; padding: 30px; background-color: #ce7c72; }

.no-touchevents a[class*="button"]:visited, .no-touchevents button[class*="button"]:visited { background-color: #ce7c72; color: #fff; text-decoration: none; }

.no-touchevents a[class*="button"]:hover, .no-touchevents button[class*="button"]:hover { color: #fff; background-color: #8f3c32; text-decoration: none; }

.no-touchevents a[class*="button"]:active, .no-touchevents button[class*="button"]:active { color: #fff; background-color: #8f3c32; text-decoration: none; }

.no-touchevents a.pr-header__geolocation-skip-button:visited, .no-touchevents a.pr-header__geolocation-start-button:visited { background: rgba(0, 0, 0, 0.5); color: #fff; text-decoration: none; }

.no-touchevents a.pr-header__geolocation-skip-button:hover, .no-touchevents a.pr-header__geolocation-start-button:hover { background: rgba(42, 151, 19, 0.5); }

.no-touchevents a.pr-header__geolocation-skip-button:active, .no-touchevents a.pr-header__geolocation-start-button:active { background: rgba(42, 151, 19, 0.5); color: #fff; text-decoration: none; }

