.no-touchevents .pr-promo__appstore:hover, .no-touchevents .pr-promo__google-play:hover, .no-touchevents .pr-promo__appstore:active, .no-touchevents .pr-promo__google-play:active { opacity: 0.7; }

.no-touchevents .footer__appstore:visited, .no-touchevents .footer__google-play:visited { opacity: 1; }

.no-touchevents .footer__appstore:hover, .no-touchevents .footer__google-play:hover, .no-touchevents .footer__appstore:active, .no-touchevents .footer__google-play:active { opacity: 0.7; }

.no-touchevents .footer__social-network-button:visited { opacity: 1; }

.no-touchevents .footer__social-network-button:hover, .no-touchevents .footer__social-network-button:active { opacity: 0.7; }

.touchevents a:visited { color: inherit; }

.touchevents a:hover { color: inherit; }

.touchevents a[class*="button"]:visited, .touchevents button[class*="button"]:visited { background-color: #ce7c72; color: #fff; text-decoration: none; }

.touchevents a[class*="button"]:hover, .touchevents button[class*="button"]:hover { background-color: #ce7c72; color: #fff; text-decoration: none; }

.touchevents a[class*="button"]:active, .touchevents button[class*="button"]:active { background-color: #8f3c32; color: #fff; text-decoration: none; }

.touchevents a.pr-header__geolocation-skip-button:hover, .touchevents a.pr-header__geolocation-start-button:hover, .touchevents a.pr-header__geolocation-skip-button:visited, .touchevents a.pr-header__geolocation-start-button:visited { background: rgba(0, 0, 0, 0.5); }

.touchevents a.pr-header__geolocation-skip-button:active, .touchevents a.pr-header__geolocation-start-button:active { background: rgba(42, 151, 19, 0.5); color: #fff; text-decoration: none; }

.touchevents .pr-promo__appstore:hover, .touchevents .pr-promo__google-play:hover { opacity: 1; }

.touchevents .pr-promo__appstore:active:before, .touchevents .pr-promo__google-play:active:before { background: rgba(0, 0, 0, 0.5); }

.touchevents .footer__appstore:hover, .touchevents .footer__google-play:hover { opacity: 1; }

.touchevents .footer__appstore:active:before, .touchevents .footer__google-play:active:before { background: rgba(0, 0, 0, 0.5); }

.touchevents .footer__social-network-button:hover { opacity: 1; }

.touchevents .footer__social-network-button:active:before { background: rgba(0, 0, 0, 0.5); }

.header { padding: 8px 0 57px 0; background-color: #4b5257; background-image: url(../img/header-bg.jpg); background-size: cover; background-repeat: no-repeat; position: relative; }

.header__logo { padding: 0 15px; max-width: 150px; height: 70px; display: flex; justify-content: flex-start; align-items: center; }

.header__logo-image { display: block; height: auto; max-width: 100%; max-height: 70px; }

.header__slogan { display: block; margin-top: 10px; font-size: 10px; text-transform: uppercase; color: #fff; }

.header__phone-number { display: inline-block; position: relative; color: #fff; font-size: 20px; padding: 0 15px; text-decoration: none; }

.header__phone-number:before { content: ""; display: inline-block; background: url(../img/phone-icon.png) center center no-repeat; background-size: contain; position: relative; width: 22px; height: 22px; left: 0; top: 3px; margin-right: 5px; }

.header__phone-number:hover { color: #fff; text-decoration: none; }

.header__navbar { width: 100%; background: rgba(0, 0, 0, 0.7); position: absolute; bottom: 0; left: 0; max-height: 50px; }

.header__navbar_fixed { position: fixed; top: 0; left: 0; z-index: 1000; }

.header__navbar-items { font-size: 14px; font-weight: 300; list-style-type: none; padding: 0; margin: 0; display: flex; flex-direction: row; justify-content: space-between; align-items: center; justify-content: flex-start; color: #fff; }

.header__navbar-items li { height: 50px; -webkit-font-smoothing: subpixel-antialiased; }

.header__navbar-items a { color: black; text-decoration: underline; color: inherit; display: flex; flex-direction: column; align-items: center; justify-content: center; height: 100%; text-decoration: none; }

.header__navbar-items a:hover { color: #ce7c72; }

.header__navbar-items a:focus { outline: 0; }

.header__navbar-items a:hover { color: #d9d9d9; }

.header__navbar-items_buttons { justify-content: flex-end; }

.header__navbar-items_buttons li { margin-left: 10px; }

.header__navbar-item_cart { position: relative; }

.header__navbar-item_cart .header__navbar-item_cart-counter { top: 5px !important; }

.header__navbar_fixed .header__navbar-items_buttons li { margin-left: 45px; }

.header__navbar_fixed .header__navbar-items_buttons a { display: block; width: 40px; }

.header__navbar_fixed .header__navbar-items_buttons a:hover { opacity: 0.8; }

.header__navbar_fixed .header__navbar-items_buttons a span { display: none; }

.header__navbar_fixed .header__navbar-item_personal-account { background: url(../img/account-icon.svg) no-repeat center center; flex: 1; }

.header__navbar_fixed .header__navbar-item_cart { background: url(../img/cart-icon.svg) no-repeat center center; flex: 1; }

.header__navbar_fixed .header__navbar-item_cart .header__navbar-item_cart-counter { display: flex; }

.header__navbar_fixed .header__navbar-item_logout { background: url(../img/log_out-icon.svg) no-repeat center center; flex: 1; }

.header__navbar_fixed .header__navbar-item_orders-history { background: url(../img/orders_history-icon.svg) no-repeat center center; flex: 1; }

.header__breadcrumbs-item_unactive { padding-left: 5px; }

@media (max-width: 992px) { .header__navbar-items { font-size: 12px; } }

.header__back-wrapper { width: 40px; height: 40px; display: flex; align-items: center; justify-content: center; }

.header__back { border: 0; outline: 0; border-radius: 0; background: transparent; padding: 0; display: block; background: transparent; width: 28px; height: 28px; cursor: pointer; }

.header__back:active, .header__back:focus { border: 0; outline: 0; }

.header__back span { display: block; width: 100%; height: 100%; background: #fff; clip-path: polygon(66% 8%, 17% 50%, 67% 92%, 57% 100%, 0% 50%, 57% 0); }

.header__mobile-menu-burger-wrapper { width: 40px; height: 40px; display: flex; align-items: center; }

.header__mobile-menu-burger { display: inline-block; width: 28px; height: 28px; margin: 0; border: 0; outline: 0; background: transparent; border-radius: 0; padding: 0; cursor: pointer; }

.header__mobile-menu-burger:focus, .header__mobile-menu-burger:active { border: 0; outline: 0; }

.header__mobile-menu-burger span { display: block; height: 3px; width: 100%; background: #fff; position: relative; }

.header__mobile-menu-burger span:before { content: ""; display: block; height: 3px; width: 100%; background: #fff; position: absolute; top: -10px; }

.header__mobile-menu-burger span:after { content: ""; display: block; height: 3px; width: 100%; background: #fff; position: absolute; top: 10px; }

.header__mobile-cart-wrapper { width: 40px; height: 40px; display: flex; align-items: center; justify-content: flex-end; }

.header__mobile-cart { position: relative; display: block; width: 28px; height: 28px; background: url(../img/cart-icon.svg) no-repeat center center; background-size: cover; color: black; text-decoration: underline; }

.header__mobile-cart:hover { color: #ce7c72; }

.header__cart-counter, .header__navbar-item_cart .header__navbar-item_cart-counter { display: flex; flex-direction: row; justify-content: center; align-items: center; position: absolute; top: -5px; right: -5px; width: 20px; height: 20px; border-radius: 50%; background: #ce7c72; color: #fff; font-size: 9px; }

.header__navbar-items_breadcrumbs { margin-left: -5px; font-size: 14px; }

.header__navbar-items_breadcrumbs .header__navbar-item { padding: 0 5px 0 5px; position: relative; }

.header__navbar-items_breadcrumbs .header__navbar-item:after { content: "/"; position: absolute; right: -3px; top: 50%; transform: translateY(-50%); }

.header__navbar-items_breadcrumbs .header__navbar-item_unactive { display: flex; flex-direction: column; align-items: center; justify-content: center; height: 100%; padding: 0 5px 0 5px; }

@media (max-width: 992px) { .header__navbar-items_breadcrumbs { font-size: 12px; } }

@media (max-width: 992px) { .header__phone-number:before { left: -20px; } }

@media (max-width: 1200px) { .header__phone-number { font-size: 18px; flex: none; white-space: nowrap; }
  .header__phone-number:before { width: 20px; height: 20px; } }

@media (max-width: 992px) { .header__phone-number { min-width: 170px; font-size: 15px; }
  .header__phone-number:before { width: 17px; height: 17px; left: 0; } }

@media (max-width: 767px) { .header { padding: 10px 0; }
  .header__mobile-menu-button { flex: none; }
  .header__back-button { flex: none; }
  .header__logo { flex: none; height: 40px; }
  .header__logo-image { max-height: 40px; }
  .header__search { display: block; flex: none; width: 40px; height: 40px; } }

@media (max-width: 575px) { .header { position: fixed; top: 0; left: 0; width: 100%; z-index: 850; padding: 10px 15px; } }

/*стили хедера*/
.mobile-menu { background: rgba(0, 0, 0, 0.9); width: 100%; height: 100%; position: fixed; top: 0; left: 0; bottom: 0; right: 0; z-index: 900; transform: translate(-100%, 0); transition: all 0.3s ease-in; padding: 80px 15px 15px 15px; overflow: scroll; -webkit-overflow-scrolling: touch; }

.mobile-menu_open { transform: translate(0, 0); transition: all 0.3s ease-in; }

.mobile-menu__close { border: 0; outline: 0; border-radius: 0; background: transparent; padding: 0; display: block; position: absolute; top: 15px; right: 15px; width: 34px; height: 34px; background: url(../img/close_menu.png) no-repeat; background-size: cover; cursor: pointer; }

.mobile-menu__close:active, .mobile-menu__close:focus { border: 0; outline: 0; }

.mobile-menu__select { position: relative; }

.mobile-menu__select:before { content: ""; display: block; width: 0; height: 0; border-right: 4px solid transparent; border-left: 4px solid transparent; border-top: 8px solid white; position: absolute; top: 50%; transform: translateY(-50%); left: 0; }

.mobile-menu__select select { -webkit-appearance: none; -moz-appearance: none; text-indent: 0.01px; -ms-appearance: none; cursor: pointer; padding: 6px 0 6px 15px; color: #fff; background: transparent; border: 0; outline: 0; display: block; font-size: 16px; margin: 0 0 20px 0; border-bottom: 2px solid #E2DDDD; border-radius: 0; box-sizing: border-box; width: 100%; font-weight: 300; }

.mobile-menu__item { color: #fff; font-size: 18px; text-align: left; display: inline-block; width: 100%; background: transparent; margin: 0 0 35px 0; font-weight: 300; cursor: pointer; }

.mobile-menu__item:visited { color: #fff !important; }

.mobile-menu__item:hover { text-decoration: none; color: #bfbfbf; }

.mobile-menu__text { padding-top: 15px; position: relative; font-weight: 500; color: #fff; text-align: left; margin-bottom: 35px; font-size: 18px; }

.mobile-menu__note { font-size: 12px; color: #fff; position: absolute; top: -10px; left: 0; font-weight: 300; }

.mobile-menu__buttons-wrapper { display: flex; flex-direction: row; flex-wrap: nowrap; justify-content: space-between; align-items: center; }

.mobile-menu__button { border: 0; outline: 0; border-radius: 0; background: transparent; padding: 0; display: inline-block; line-height: 1; background-color: #ce7c72; border-radius: 4px; border: 0; outline: 0; color: #fff; padding: 5px 10px; cursor: pointer; display: inline-block; width: 50%; margin: 0 0 15px 0; padding: 15px; font-size: 16px; }

.mobile-menu__button:active, .mobile-menu__button:focus { border: 0; outline: 0; }

.mobile-menu__button:first-child { margin-right: 7.5px; }

.mobile-menu__button:nth-child(2) { margin-left: 7.5px; }

/*мобильное меню*/
.header__geolocation-form { display: flex; flex-direction: row; justify-content: space-between; align-items: center; justify-content: center; padding: 0 15px; }

.header__geolocation-form .header__compositional-input { position: relative; right: -10px; }

.header__compositional-input { display: flex; flex-direction: row; justify-content: space-between; align-items: center; flex-wrap: nowrap; }

.header__input { padding: 5px 15px; height: 30px; border: 0; font-size: 14px; line-height: 1.3; }

.header__input:focus { outline: 0; }

.header__input_city { width: 35%; position: relative; z-index: 5; border-right: 1px solid #9c9c9c; }

.header__input_city:after { content: ""; display: block; width: 0; height: 0; border-right: 4px solid transparent; border-left: 4px solid transparent; border-top: 8px solid white; border-top: 8px solid black; position: absolute; right: 10px; top: 50%; transform: translateY(-50%); }

.header__input_city select { appearance: none; display: block; height: 30px; padding: 0 25px 0 10px; font-size: 14px; line-height: 1.3; border-radius: 4px 0 0 4px; border: 0; outline: 0; width: 100%; cursor: pointer; background: #fff; }

.header__input_city select:focus, .header__input_city select:active { border: 0; outline: 0; }

.header__input_city select:hover { background: #f2f2f2; }

.header__input_street { width: 45%; border-radius: 0; border-right: 1px solid #9c9c9c; position: relative; z-index: 4; left: -5px; }

.header__input_house { width: 20%; border-bottom-right-radius: 4px; border-top-right-radius: 4px; position: relative; z-index: 3; left: -10px; }

.header__geolocation-form-map-button { border: 0; outline: 0; border-radius: 0; background: transparent; padding: 0; display: inline-block; line-height: 1; background-color: #ce7c72; border-radius: 4px; border: 0; outline: 0; color: #fff; padding: 5px 10px; cursor: pointer; display: block; height: 30px; width: 30px; margin: 0 5px; background-image: url(../img/map_pin_white-icon.svg); background-size: 75%; background-repeat: no-repeat; background-position: center center; }

.header__geolocation-form-map-button:active, .header__geolocation-form-map-button:focus { border: 0; outline: 0; }

.header__geolocation-form-submit-button { border: 0; outline: 0; border-radius: 0; background: transparent; padding: 0; display: inline-block; line-height: 1; background-color: #ce7c72; border-radius: 4px; border: 0; outline: 0; color: #fff; padding: 5px 10px; cursor: pointer; display: block; height: 30px; font-size: 14px; }

.header__geolocation-form-submit-button:active, .header__geolocation-form-submit-button:focus { border: 0; outline: 0; }

.pr-header__geolocation-form .header__input { height: 60px; font-size: 18px; }

.pr-header__geolocation-form select { height: 60px; font-size: 18px; }

.pr-header__geolocation-form .header__compositional-input { margin: 10px 0 25px 0; }

/*форма гелокации в хедере*/
.geolocation-mobile { background: #ce7c72; padding: 10px 15px; display: block; width: 100%; height: 60px; margin-bottom: 15px; }

.geolocation-mobile:focus { outline: 0; text-decoration: none; }

.geolocation-mobile:hover { text-decoration: none; }

.geolocation-mobile__button { display: block; line-height: 40px; vertical-align: middle; text-align: center; font-size: 14px; font-weight: 400; color: #9e9e9e; width: 100%; background: #fff; border-radius: 4px; cursor: pointer; }

.geolocation-mobile__button:hover { background: #fcfcfc; }

.geolocation-mobile__button:after { display: inline-block; content: ""; width: 10px; height: 20px; background: url(../img/map_pin_gray-icon.svg) center center no-repeat; background-size: cover; transform: translate(5px, 4px); }

@media (max-width: 575px) { .geolocation-mobile { margin: 0 -15px 15px -15px; width: calc(100% + 30px); } }

/*геолокация и все что с ней связано для мобильных устройств*/
.dc-checkbox__pseudo-checkbox { display: block; height: 20px; width: 20px; background: #fff; border: 1px solid #efefef; border-radius: 4px; position: relative; margin-right: 10px; }

.dc-checkbox__pseudo-checkbox:before { content: ""; display: block; position: absolute; bottom: 5px; left: 3px; width: 0; height: 2px; border-radius: 1px; background: #ce7c72; transform: rotate(45deg); }

.dc-checkbox__pseudo-checkbox:after { content: ""; display: block; position: absolute; bottom: 8px; left: 5px; width: 0; height: 2px; border-radius: 1px; background: #ce7c72; transform: rotate(120deg); }

.dc-checkbox__hidden-input { display: none; }

.dc-checkbox__hidden-input + label { display: flex; flex-direction: row; justify-content: space-between; align-items: center; flex-direction: row-reverse; justify-content: flex-end; cursor: pointer; margin: 0 0 10px 0; }

.dc-checkbox__hidden-input + label:hover { color: #ce7c72; }

.dc-checkbox__hidden-input + label:hover .dc-checkbox__pseudo-checkbox { border-color: #c9c9c9; }

.dc-checkbox__hidden-input:checked + label { color: #ce7c72; }

.dc-checkbox__hidden-input:checked + label .dc-checkbox__pseudo-checkbox:before { width: 8px; }

.dc-checkbox__hidden-input:checked + label .dc-checkbox__pseudo-checkbox:after { width: 14px; }

.dc-input-block { margin: 0; }

.dc-input-block__label { display: block; margin: 0 0 15px 0; height: 65px; position: relative; }

.dc-input-block__label:hover input { border-color: #c9c9c9; }

.dc-input-block__label_select:after { content: ""; display: block; width: 0; height: 0; border-right: 4px solid transparent; border-left: 4px solid transparent; border-top: 8px solid white; border-top: 8px solid black; position: absolute; top: 50%; transform: translateY(-10px); right: 10px; }

.dc-input-block__input { border: 1px solid #efefef; border-radius: 4px; display: block; padding: 24px 10px 4px 10px; font-size: 16px; line-height: 20px; width: 100%; }

.dc-input-block__input:focus { border-color: #c9c9c9; outline: 0; }

.dc-input-block__input:focus + .dc-input-block__placeholder { transform: matrix(1, 0, 0, 1, 0, -48); }

.dc-input-block__input.value-exists + .dc-input-block__placeholder { transform: matrix(1, 0, 0, 1, 0, -48); }

.dc-input-block__placeholder { display: inline-block; font-size: 16px; color: gray; padding-left: 10px; transform: matrix(1, 0, 0, 1, 0, -38); cursor: text; transition: all 0.2s ease-in; }

.dc-input-block__error-message { display: block; color: #ff122d; font-size: 12px; font-weight: 300; transform: translateY(-25px); }

.dc-input-block__select { -moz-appearance: none; -webkit-appearance: none; border: 1px solid #efefef; border-radius: 4px; display: block; padding: 24px 10px 4px 10px; font-size: 16px; line-height: 20px; width: 100%; }

.dc-input-block__select + .dc-input-block__placeholder { transform: matrix(1, 0, 0, 1, 0, -48); }

select::-ms-expand { display: none; background: #fff; }

/*стили всех инпутов*/
.geolocation-switcher__form { display: flex; flex-direction: row; justify-content: space-between; align-items: center; border: 1px solid #efefef; border-radius: 4px; overflow: hidden; margin-bottom: 15px; }

.geolocation-switcher__form:hover { border-color: #c9c9c9; }

.geolocation-switcher__form div { width: 50%; }

.geolocation-switcher__form label { display: block; width: 100%; background: #fff; color: #9e9e9e; padding: 10px 0; text-align: center; margin: 0; cursor: pointer; font-size: 14px; }

.geolocation-switcher__form label:hover { background: #fcfcfc; }

.geolocation-switcher__form input[type="radio"] { display: none; }

.geolocation-switcher__form input[type="radio"]:checked + label { background: #ce7c72; color: #fff; }

.geolocation-switcher__form input[type="radio"]:checked + label:hover { background: #ce7c72; cursor: default; }

/*переключатель доставка/самовывоз*/
.sidebar-favorites { background: #fff; padding: 10px 10px 10px 10px; border: 1px solid #efefef; border-radius: 4px; font-size: 14px; font-weight: 400; }

.sidebar-favorites:hover { border-color: #c9c9c9; }

.sidebar-favorites__heading { display: block; color: #9e9e9e; }

.sidebar-favorites__item { display: flex; flex-direction: row; justify-content: space-between; align-items: center; position: relative; color: inherit; padding: 15px 0; border-bottom: 1px solid #efefef; }

.sidebar-favorites__item:last-child { border-bottom: 0; padding-bottom: 5px; }

.sidebar-favorites__item:hover { color: #ce7c72; text-decoration: none; }

.sidebar-favorites__item:hover .sidebar-favorites__delete { display: block; }

.sidebar-favorites__item-last { border-bottom: 0; padding-bottom: 5px; }

.sidebar-favorites__image-wrapper { min-width: 65px; width: 65px; height: 65px; margin-right: 10px; display: flex; align-items: center; }

.sidebar-favorites__name { align-self: center; flex: 2; max-height: 40px; overflow: hidden; }

.sidebar-favorites__delete { display: none; position: absolute; top: 15px; right: 0; }

.sidebar-favorites__delete .sidebar-favorites__delete-button { display: block; width: 20px; height: 20px; background: #fff; position: relative; cursor: pointer; }

.sidebar-favorites__delete .sidebar-favorites__delete-button:before { content: ""; display: block; width: 18px; height: 1px; background: #B9B9B9; position: absolute; top: 8.5px; left: 1px; transform: rotate(45deg); }

.sidebar-favorites__delete .sidebar-favorites__delete-button:after { content: ""; display: block; width: 18px; height: 1px; background: #B9B9B9; position: absolute; left: 1px; top: 8.5px; transform: rotate(-45deg); }

.sidebar-favorites__delete .sidebar-favorites__delete-button:hover:after, .sidebar-favorites__delete .sidebar-favorites__delete-button:hover:before { background: #ce7c72; }

