.restaurant-block__panel { display: flex; flex-direction: row; justify-content: space-between; align-items: center; }

.restaurant-block__tabs { font-size: 0; }

.restaurant-block__tab-link { text-decoration: none; text-transform: uppercase; font-weight: bold; font-size: 11px; display: inline-block; vertical-align: middle; padding: 10px 40px; color: #000; border-bottom: 2px solid transparent; }

.restaurant-block__tab-link.active { border-color: #ce7c72; }

.restaurant-block__tab-link:hover { text-decoration: none; color: #000; border-color: #ce7c72; }

.restaurant-block__favorites input { display: none; }

.restaurant-block__favorites label { background-repeat: no-repeat; background-image: url(../../img/heart-empty.svg); background-size: 21px 19px; width: 21px; height: 19px; margin: 0; vertical-align: middle; }

.restaurant-block__favorites input:checked + label { background-image: url(../../img/heart.svg); }

.goods-block { display: none; flex-direction: row; justify-content: space-between; }

.goods-block__item { width: 31%; border-radius: 4px; border: 1px solid #efefef; padding: 30px 18px 20px 18px; background-color: #fff; position: relative; }

.goods-block__item-img { padding-left: 20px; padding-right: 20px; }

.goods-block__item-img img { display: block; height: auto; width: 100%; }

.goods-block__item-title { font-size: 18px; line-height: 24px; margin-top: 10px; margin-bottom: 20px; font-weight: bold; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }

.goods-block__item-panel { display: flex; flex-direction: row; justify-content: space-between; align-items: center; }

.goods-block__item:hover .goods-tooltip { display: block; }

.goods-block__price { font-weight: bold; font-size: 18px; line-height: 26px; }

.goods-block__to-cart { border: 0; outline: 0; background: transparent; padding: 0; display: inline-block; line-height: 1; background-color: #ce7c72; border-radius: 4px; border: 0; outline: 0; color: #fff; padding: 5px 10px; cursor: pointer; height: 30px; font-size: 14px; }

.goods-block__to-cart:hover { background-color: #8f3c32; }

.goods-block.active { display: flex; }

.goods-tooltip { display: none; position: absolute; background-color: #fff; border: 1px solid #efefef; z-index: 3; min-width: 325px; right: 101%; bottom: 0; }

.goods-tooltip:after { content: ''; position: absolute; top: 60%; right: -19px; width: 0; height: 0; border-style: solid; border-width: 30px 0 30px 19px; border-color: transparent transparent transparent #efefef; z-index: 4; }

.goods-tooltip:before { content: ''; position: absolute; top: 60%; right: -18px; width: 0; height: 0; border-style: solid; border-width: 30px 0 30px 19px; border-color: transparent transparent transparent #fff; z-index: 5; }

.goods-tooltip--first { left: 101%; }

.goods-tooltip--first:after { left: -19px; border-width: 30px 19px 30px 0; border-color: transparent #efefef transparent transparent; }

.goods-tooltip--first:before { left: -18px; border-width: 30px 19px 30px 0; border-color: transparent #fff transparent transparent; }

.goods-tooltip__img { display: block; height: auto; width: 100%; }

.goods-tooltip__descr { font-size: 14px; text-align: center; padding: 15px 15px; margin: 0; }

.goods-news { display: none; }

.goods-news__item { display: flex; width: 100%; flex-direction: row; justify-content: space-between; padding: 15px; border-radius: 4px; border: 1px solid #efefef; background-color: #fff; margin-bottom: 10px; }

.goods-news__img { width: 100px; margin-right: 20px; }

.goods-news__img img { display: block; height: auto; width: 100%; }

.goods-news__title { font-weight: bold; font-size: 18px; line-height: 18px; margin-bottom: 20px; margin-top: 0; overflow: hidden; white-space: nowrap; text-overflow: ellipsis; }

.goods-news__info { width: calc(100% - 120px); }

.goods-news__descr { font-size: 14px; line-height: 16px; margin-bottom: 0; }

.goods-news__btn-wrap { text-align: right; }

.goods-news__more { color: #ce7c72; font-size: 14px; text-decoration: none; }

.goods-news__more:hover { text-decoration: none; color: #8f3c32; }

.goods-news.active { display: block; }

body { background-color: #f5f5f5 !important; }

hr { border: 0; display: block; height: 1px; background: #DBDBDB; }

.companies-page { padding: 0 56px; }

h5, h4, h3, h2, h1 { font-weight: normal; }

.modal-open { overflow-y: visible !important; padding-right: 0 !important; }

@media (max-width: 575px) { .content { padding: 75px 0 30px 0; }
  .companies-page { padding: 0 15px; }
  .modal-open { overflow: hidden; padding-right: 0 !important; } }

.hide-before-load { visibility: hidden; }

a:focus { color: inherit; text-decoration: none; }

a[href^="tel"] { text-decoration: none; }

.owl-item { float: left; }
